import React from "react";

import slide0 from "../resources/slides/slide-0.png"
import slide1 from "../resources/slides/slide-01.jpg"
import slide2 from "../resources/slides/slide-02.jpg"
import slide3 from "../resources/slides/slide-03.jpg"
import Carousel from 'react-bootstrap/Carousel'

function Banner() {
    const images = [slide1, slide2, slide3]
    const slides = [/*{
        'src': slide0,
        'title': 'Title One',
        'subTitle': 'Sub Title One'
    },*/{
        'src': slide1,
        'title': 'Kairos',
        'subTitle': 'That propitious moment'
    }, {
        'src': slide2,
        'title': 'Kairos',
        'subTitle': 'Its the time'
    }, {
        'src': slide3,
        'title': 'Kairos',
        'subTitle': 'It will work'
    }]

    function d(img, heading, subHeading) {
        return (

            <Carousel.Item>
                <img
                    className="d-block w-100"
                    src={img}
                    alt="First slide"
                />
                <Carousel.Caption>
                    <h3>{heading}</h3>
                    <p>{subHeading}</p>
                </Carousel.Caption>
            </Carousel.Item>

        )
    }

    return (
        <>
            <Carousel>
                {slides.map(value => d(value.src, value.title, value.subTitle))}
            </Carousel>

        </>
    )


}

export default Banner